<template lang="pug">
  #app
    h2.super-title ÁïôË®ÄÊùøÊùø
    .words-edit
      .hinter üôâ ‰Ω†ÂØπÂú®Â∫ßÁöÑÂêÑ‰ΩçÊúâ‰ªÄ‰πàË¶ÅËØ¥ÁöÑÔºü
      textarea(ref='input', :disabled='sending', maxLength='1024', v-model='text', rows='3', placeholder='ÂØåÂº∫„ÄÅÊ∞ë‰∏ª„ÄÅÊñáÊòé„ÄÅÂíåË∞ê\r\nËá™Áî±„ÄÅÂπ≥Á≠â„ÄÅÂÖ¨Ê≠£„ÄÅÊ≥ïÊ≤ª\r\nÁà±ÂõΩ„ÄÅÊï¨‰∏ö„ÄÅËØö‰ø°„ÄÅÂèãÂñÑ')
      .bottom
        button(:disabled='!text.length || sending', @click='handleSubmit')
          span(v-if='sending') SENDING...
          span(v-else) SUBMIT ({{ text.length }}/1024)
    .words-main
      words-list(:data='list', @need-refresh='fetchWordsList', :loaded='loaded', :ban='ban', :loading='loading')
</template>

<script>
import { fetchWordsList, submitWord } from '@/api/app'

export default {
  name: 'app',
  mounted() {
    this.fetchWordsList()
    this.$refs.input.focus()
  },
  data() {
    return {
      sending: false,
      text: '',
      list: [],
      ban: null,
      loaded: false,
      loading: false
    }
  },
  methods: {
    async fetchWordsList() {
      try {
        this.loading = true
        let res = await fetchWordsList()
        this.loading = false
        this.list = res
        this.loaded = true
        this.ban = null
      } catch (err) {
        this.loaded = true
        this.loading = false
        this.ban = `Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥• (${err.response.status})`
      }
    },
    async handleSubmit() {
      try {
        this.sending = true
        await submitWord(this.text.slice(0, 1024))
        this.fetchWordsList()
        this.text = ''
        this.sending = false
      } catch (err) {
        this.sending = false
        alert(`ÁïôË®ÄÂ§±Ë¥• (${err.response.status})`)
      }
    }
  }
}
</script>
