/* eslint-disable */

function log() {
  console.log(...arguments)
}

/**
 * @description ËøîÂõûËøô‰∏™Ê†∑ÂºèÁöÑÈ¢úËâ≤ÂÄº
 * @param {String} type Ê†∑ÂºèÂêçÁß∞ [ primary | success | warning | danger | text ]
 */
function typeColor(type = 'default') {
  let color = ''
  switch (type) {
    case 'default':
      color = '#35495E'
      break
    case 'primary':
      color = '#3488ff'
      break
    case 'success':
      color = '#43B883'
      break
    case 'warning':
      color = '#e6a23c'
      break
    case 'danger':
      color = '#f56c6c'
      break
    default:
      break
  }
  return color
}

/**
 * @description ÊâìÂç∞‰∏Ä‰∏™ [ title | text ] Ê†∑ÂºèÁöÑ‰ø°ÊÅØ
 * @param {String} title title text
 * @param {String} info info text
 * @param {String} type style
 */
log.capsule = function(title, info, type = 'primary') {
  console.log(
    `%c ${title} %c ${info} %c`,
    'background:#35495E; padding: 1px; border-radius: 3px 0 0 3px; color: #fff;',
    `background:${typeColor(
      type
    )}; padding: 1px; border-radius: 0 3px 3px 0;  color: #fff;`,
    'background:transparent'
  )
}

/**
 * @description ÊâìÂç∞ÂΩ©Ëâ≤ÊñáÂ≠ó
 */
log.colorful = function(textArr) {
  console.log(
    `%c${textArr.map(t => t.text || '').join('%c')}`,
    ...textArr.map(t => `color: ${typeColor(t.type)};`)
  )
}

/**
 * @description ÊâìÂç∞ default Ê†∑ÂºèÁöÑÊñáÂ≠ó
 */
log.default = function(text) {
  log.colorful([{ text }])
}

/**
 * @description ÊâìÂç∞ primary Ê†∑ÂºèÁöÑÊñáÂ≠ó
 */
log.primary = function(text) {
  log.colorful([{ text, type: 'primary' }])
}

/**
 * @description ÊâìÂç∞ success Ê†∑ÂºèÁöÑÊñáÂ≠ó
 */
log.success = function(text) {
  log.colorful([{ text, type: 'success' }])
}

/**
 * @description ÊâìÂç∞ warning Ê†∑ÂºèÁöÑÊñáÂ≠ó
 */
log.warning = function(text) {
  log.colorful([{ text, type: 'warning' }])
}

/**
 * @description ÊâìÂç∞ danger Ê†∑ÂºèÁöÑÊñáÂ≠ó
 */
log.danger = function(text) {
  log.colorful([{ text, type: 'danger' }])
}

export default log
